import React from 'react';
import { View } from 'react-native';
import styled from '@stream-io/styled-components';
import PropTypes from 'prop-types';
import { Spinner } from './Spinner';
import { withTranslationAndStatics } from '../utils';

const LoadingText = styled.Text`
  margin-top: 20px;
  font-size: 14px;
  font-weight: 600;
`;

export const LoadingIndicator = withTranslationAndStatics()(
  class LoadingIndicator extends React.PureComponent {
    static propTypes = {
      listType: PropTypes.oneOf(['channel', 'message', 'default']),
    };

    static defaultProps = {
      listType: 'default',
    };

    render() {
      switch (this.props.listType) {
        case 'channel':
          return (
            <View
              style={{
                flex: 1,
                justifyContent: 'center',
                alignItems: 'center',
              }}
            >
              <Spinner />
              <LoadingText>
                {this.props.t('chat.loadingIndicator.channel')}
              </LoadingText>
            </View>
          );
        case 'message':
          return (
            <View
              style={{
                flex: 1,
                justifyContent: 'center',
                alignItems: 'center',
              }}
            >
              <Spinner />
              <LoadingText>
                {this.props.t('chat.loadingIndicator.message')}
              </LoadingText>
            </View>
          );
        case 'default':
        default:
          return (
            <View
              style={{
                flex: 1,
                justifyContent: 'center',
                alignItems: 'center',
              }}
            >
              <Spinner />
              <LoadingText>
                {this.props.t('chat.loadingIndicator.default')}
              </LoadingText>
            </View>
          );
      }
    }
  },
);
